.owl-carousel {
  .owl-nav {
    text-align: center;
    .owl-prev,
    .owl-next {
      width: 30px;
      height: 30px;
      background: #FFF !important;
      border-radius: 50%;
      border: 1px solid #000 !important;
      position: relative;
      margin: 0 1.5px;
      outline: none;
      transition: all 0.4s;
      span {
        background-image: url(../images/chevron-down-solid.svg);
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        width: 25px;
        height: 25px;
        display: inline-block;
        vertical-align: middle; } }
    .owl-prev {
      span {
        transform: rotate(90deg); }
      &:before {
        content: "";
        width: 50px;
        height: 1px;
        background: #000000;
        position: absolute;
        top: calc(50% - 1px);
        left: -50px;
        transition: all 0.4s; }
      &:after {
        content: "";
        width: 30px;
        height: 1px;
        background: #000000;
        position: absolute;
        top: calc(50% + 3px);
        left: -30px;
        transition: all 0.4s; }
      &:hover {
        background: #4c834a;
        border-color: #4c834a;
        &:before {
          width: 30px;
          left: -30px; }
        &:after {
          left: -50px;
          width: 50px; } } }
    .owl-next {
      span {
        transform: rotate(270deg); }
      &:before {
        content: "";
        width: 50px;
        height: 1px;
        background: #000000;
        position: absolute;
        top: calc(50% - 1px);
        right: -50px;
        transition: all 0.4s; }
      &:after {
        content: "";
        width: 30px;
        height: 1px;
        background: #000000;
        position: absolute;
        top: calc(50% + 3px);
        right: -30px;
        transition: all 0.4s; }
      &:hover {
        background: #4c834a;
        border-color: #4c834a;
        &:before {
          width: 30px;
          right: -30px; }
        &:after {
          right: -50px;
          width: 50px; } } } } }


