    padding: 6.3% 0;
    display: block; }
  &:after {
    content: "";
    width: 100%;
    height: 100%;
    background: #000;
    opacity: 0.8;
    position: absolute;
    top: 0;
    left: 0; } }

.title-breadcrumbs {
